import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Row, Col } from 'antd';
import InfoCountTotalCard from '../../../components/common/InfoCard/index';
import {
  getQuickLinkClassGetClassNo,
  getQuickLinkClassGetClassOwnerNo,
  getQuickLinkClassGetClassResponsibleNo,
  getQuickLinkClassGetStudentNo,
  getQuickLinkClassGetStudentTitleNo,
  getQuickLinkClassGetStudentWithDormNo,
  getQuickLinkClassGetStudentWithOutDormNo,
} from '../../../services/quicklink';

const COLORS = [
  '#0088FE',
  '#00C49F',
  '#FFBB28',
  '#FF8042',
  '#0088fed9',
  '#00c49fd9',
  '#ffbb28d9',
  '#ff8042d9',
];

const data = [
  {
    title: 'Lớp học',
    service: getQuickLinkClassGetClassNo,
    url: '/classes/list',
  },
  {
    title: 'GVCN & cố vấn',
    service: getQuickLinkClassGetClassOwnerNo,
    url: '/homeroom-teacher/list',
  },
  {
    title: 'Cán bộ lớp',
    service: getQuickLinkClassGetClassResponsibleNo,
    url: '/listClassOfficers',
  },
  {
    title: 'Sinh viên đang học',
    service: getQuickLinkClassGetStudentNo,
    url: '/briefOfStudent/list',
  },
  {
    title: 'Chức vụ lớp',
    service: getQuickLinkClassGetStudentTitleNo,
    url: '/studentTitle/list',
  },
  {
    title: 'Sinh viên nội trú',
    service: getQuickLinkClassGetStudentWithDormNo,
    url: '/in-out-student/list',
  },
  {
    title: 'Sinh viên ngoại trú',
    service: getQuickLinkClassGetStudentWithOutDormNo,
    url: '/in-out-student/list',
  },
];

class DashboardManageClass extends Component {
  state = {};
  render() {
    return (
      <Row gutter={24}>
        {data.map((item, index) => (
          <Col span={6} key={item.title}>
            <InfoCountTotalCard
              backgroundColor={COLORS[index % 8]}
              title={item.title}
              service={item.service}
              icon={item.icon}
              url={item.url}
            />
          </Col>
        ))}
      </Row>
    );
  }
}

export default props => (
  <Page {...props}>
    <DashboardManageClass {...props} />
  </Page>
);



